/* @import-normalize bring in normalize.css styles  */

:root {
  --main-text-color: #000000;
  --second-text-color: #ffffff;
  --accent-text-color: #008000;

  --main-bg-color: #ffffff;
  --second-bg-color: #7c0606;
  --accent-bg-color: #008000;

  --notification-message: #cccccc;
  --notification-message-green: rgba(0, 128, 0, 0.2);
  --notification-message-red: rgba(255, 0, 0, 0.2);

  --main-animation-hover: 250ms linear;
  --main-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.5);
}

body {
  margin: 0;
  display: flex;
  justify-content: center;
  padding: 100px;
  background-color: var(--main-bg-color);
  color: var(--main-text-color);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
